import React, { Component } from 'react';
import './Styles.css';

import Eos from 'eosjs';
import {
    Layout, Table, Form, Icon, Input, Button, Message, Tooltip, Switch
} from 'antd';
import {
    kylin_net, main_net, info_contract_account, EditableFormRow, EditableCell,
} from '../Common';

const { Header, Content } = Layout;
const FormItem = Form.Item;

class Trustbetinfo extends Component {
    constructor(props) {
        super(props);

        this.state = {
            is_login: false,
            login_account: 'Login',
            partners: [],
            records: [],
            loading_partners: true,
            loading_records: true,
        }

        this.pay_json = [];
        this.pay_memo = '';
        this.trx_option = {};
        this.partner_old = {};
        this.network = main_net;
        this.today = Math.floor( Date.now() / (1000 * 86400) );
        this.columns_parts = [{
            title: 'ID',
            dataIndex: 'pid',
            key: 'pid',
            align: 'center',
        }, {
            title: (<span>账号 <Tooltip placement='top' title='点击账号可修改'><Icon type='info-circle' /></Tooltip></span>),
            dataIndex: 'partner',
            key: 'partner',
            align: 'center',
            editable: true,
        }, {
            title: (<span>返现率(万分之) <Tooltip placement='top' title='点击返现率可修改'><Icon type='info-circle' /></Tooltip></span>),
            dataIndex: 'rate',
            key: 'rate',
            align: 'center',
            editable: true,
        }, {
            title: (<span>操作 <Tooltip placement='top' title='修改账号或返现率后进行更新'><Icon type='info-circle' /></Tooltip></span>),
            dataIndex: 'operation',
            key: 'operation',
            align: 'center',
            render: (text, record) => (
                <a href="javascript:;" onClick={() => {this.updPartner(record)}}>更新</a>
            ),
        }];

        this.recordsSelection = {
            // onChange: ( selectedRowKeys, selectedRows ) => {
            //     console.log( 'onChange:', `selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows );
            // },
            onSelect: ( record, selected, selectedRows ) => {
                // console.log( 'onSelect=>', 'record: ', record, 'selected: ', selected, 'selectedRows: ', selectedRows );
                if ( selected && record.day === this.today ) {
                    Message.warning('不能结算当天的');
                } else {
                    this.updPayJson( record, selected );
                }
            },
            onSelectAll: ( selected, selectedRows, changeRows ) => {
                // console.log( 'onSelectAll:', 'selected: ', selected, 'selectedRows: ', selectedRows, 'changeRows: ', changeRows );
                let records;
                if ( selected ) {
                    records = selectedRows;

                    for ( let i = 0; i < records.length; i++ ) {
                        if ( records[i].day === this.today ) {
                            Message.warning('不能结算当天的');
                            records.splice( i, 1 );  // 删除该条
                        }
                    }
                }
                else {
                    records = changeRows;
                }

                for ( let record of records ) {
                    this.updPayJson( record, selected );
                }
            },
            getCheckboxProps: ( record ) => {
                let ret = { disabled: false, };
                if ( record.day === this.today ) { ret.disabled = true; }  // 当天的记录不可选
                return ret;
            },
        };
    }

    updPayJson = ( record=null, selected=true ) => {
        let _pid = record.pid, _day = record.day;

        if ( this.pay_json[_pid] ) {
            let index = this.pay_json[_pid].indexOf(_day);

            if ( index < 0 ) {
                if ( selected ) {
                    let tmp = this.pay_json[_pid];  // concat不会改变原数组
                    this.pay_json[_pid] = tmp.concat(_day);
                }
            } else {
                if ( !selected ) { this.pay_json[_pid].splice(index, 1); }  // 删除该条
            }
        } else if ( selected ) {
            this.pay_json[_pid] = [_day];
        }

        // console.log('update => pay_json: ', this.pay_json);
    }

    accountLoginLogout = () => {
        if ( !this.scatter ) {
            Message.info('请先安装Scatter~');
            return;
        }

        if ( this.state.is_login && this.scatter && this.scatter.identity ) {
            // 进入这里是logout动作
            this.scatter.forgetIdentity().then(() => {
                this.setState({ is_login: false });
                this.setState({ login_account: 'Login' });
            });
        } else if ( !this.state.is_login && this.scatter && !this.scatter.identity ) {
            // 进入这里是login动作
            this.scatter.getIdentity({ accounts: [this.network] }).then(() => {
                const account = this.scatter.identity.accounts.find(account => account.blockchain === 'eos');
                if ( account.name !== info_contract_account ) {
                    Message.warning('该账号不是管理员账号');
                }

                this.trx_option = {authorization: [`${account.name}@${account.authority}`]};
                this.setState({ login_account: account.name });
                this.setState({ is_login: true });
            });
        }
    }

    getPartners = () => {
        this.eosjs.getTableRows({
            json: true,
            code: info_contract_account,
            scope: info_contract_account,
            table: 'parts',
            // limit: 30,
        })
        .then(data => {
            if ( data.rows && data.rows.length > 0 ) {
                this.setState({ partners: data.rows });
                this.getRecords( data.rows, 0 );
            }

            this.setState({ loading_partners: false });
        })
        .catch(err => {
            console.error(err);
            this.setState({ loading_partners: false });
        });
    }

    getRecords = ( parts, next=0 ) => {
        this.eosjs.getTableRows({
            json: true,
            code: info_contract_account,
            scope: parts[next].pid,
            table: 'records',
            // limit: 30,
        })
        .then(data => {
            if ( next ) {
                this.setState((prevState) => {
                    let _records = prevState.records;
                    _records.push(data.rows);
                    return {records: _records}
                });
            } else {
                let _records = [];
                _records.push(data.rows);
                this.setState({ records: _records });
            }

            if ( ++next < parts.length ) {
                this.getRecords( parts, next );
            } else {
                this.setState({ loading_records: false });
            }
        })
        .catch(err => {
            console.error(err);
            this.setState({ loading_records: false });
        });
    }

    addPartner = (e) => {
        e.preventDefault();

        this.props.form.validateFields((err, values) => {
            if (!err) {
                if ( this.state.is_login && info_contract_account === this.state.login_account ) {
                    let eos = this.scatter.eos(this.network, Eos, {});
                    eos.contract(info_contract_account)
                        .then(contract => {
                            contract.addpart(
                                values.partName,
                                values.partRate,
                                this.trx_option,
                            )
                            .then(res => {
                                this.getPartners();
                                Message.success('添加成功~');
                            })
                            .catch(err => {
                                Message.error('添加失败!');
                            });
                        })
                        .catch(err => {
                            console.error(err);
                        });
                } else {
                    Message.error(`请登录${info_contract_account}账号进行该操作！`);
                }
            }
        });
    }

    updPartner = (newp) => {
        if ( !this.state.is_login || info_contract_account !== this.state.login_account ) {
            Message.error(`请登录${info_contract_account}账号进行该操作！`);
            return;
        }

        if ( (newp && this.partner_old) &&
             (newp.pid || newp.pid >= 0) && (newp.rate || newp.rate) >= 0 && newp.partner &&
             (this.partner_old.pid || this.partner_old.pid >= 0) && (this.partner_old.rate || this.partner_old.rate >= 0) && this.partner_old.partner ) {

            if ( newp.pid === this.partner_old.pid ) {
                if ( newp.rate !== this.partner_old.rate || newp.partner !== this.partner_old.partner ) {
                    let _pid = newp.pid;
                    let _newr = newp.rate, _newp = newp.partner;
                    let _oldr = this.partner_old.rate, _oldp = this.partner_old.partner;

                    let eos = this.scatter.eos(this.network, Eos, {});
                    eos.contract(info_contract_account)
                        .then(contract => {
                            contract.updpart(
                                _pid,
                                _oldp, _newp,
                                _oldr, _newr,
                                this.trx_option,
                            )
                            .then(res => {
                                this.getPartners();
                                Message.success('更新成功~');
                            })
                            .catch(err => {
                                Message.error('更新失败!');
                            });
                        })
                        .catch(err => {
                            console.error(err);
                        });
                } else {
                    Message.info('没有需要更新的~');
                }
            } else {
                Message.warning('请点击刚才修改那一行的更新！');
            }
        } else {
            Message.warning('请先修改账号或返现率！');
        }
    }

    pay = () => {
        if ( !this.state.is_login || info_contract_account !== this.state.login_account ) {
            Message.error(`请登录${info_contract_account}账号进行该操作！`);
            return;
        }

        // console.log('pay_json: ', this.pay_json);
        this.pay_memo = '';
        let len = this.pay_json.length;

        for ( let i = 0; i < len; i++ ) {
            // console.log(`this.pay_json[${i}]: `, this.pay_json[i]);
            if ( this.pay_json[i] && this.pay_json[i].length > 0 ) {
                if ( this.pay_memo.length > 0 ) { this.pay_memo += ';'; }

                this.pay_memo = this.pay_memo + i + ':' + this.pay_json[i].join(':');
            }
            // console.log(`${i} => pay_memo: `, this.pay_memo);
        }
        // console.log('pay_memo: ', this.pay_memo);

        if ( this.pay_memo.length > 0 ) {
            let eos = this.scatter.eos(this.network, Eos, {});
            eos.contract(info_contract_account)
                .then(contract => {
                    contract.pay(
                        this.pay_memo,
                        this.trx_option,
                    )
                    .then(res => {
                        this.pay_json = [];
                        this.pay_memo = '';
                        this.getPartners();
                        Message.success('结算成功~');
                    })
                    .catch(err => {
                        Message.error('结算失败！');
                    });
                })
                .catch(err => {
                    console.error(err);
                });
        } else {
            Message.warning('请勾选要结算的项!');
        }
    }

    switchNet = ( checked ) => {
        if ( checked ) {
            this.network = main_net;
        } else {
            this.network = kylin_net;
        }

        this.eosjs = Eos({
            httpEndpoint: `${this.network.protocol}://${this.network.host}:${this.network.port}`,
            chainId: this.network.chainId,
        });

        this.resetState();
        this.getPartners();

        if (this.scatter && this.scatter.identity) {
            const account = this.scatter.identity.accounts.find(account => account.blockchain === 'eos');
            if ( account.name !== info_contract_account ) {
                Message.warning('该账号不是管理员账号');
            }

            this.trx_option = {authorization: [`${account.name}@${account.authority}`]};
            this.setState({ login_account: account.name });
            this.setState({ is_login: true });
        }
    }

    resetState = () => {
        this.pay_json = [];
        this.pay_memo = '';
        this.trx_option = {};
        this.partner_old = {};

        this.setState({
            is_login: false,
            login_account: 'Login',
            partners: [],
            records: [],
            loading_partners: true,
            loading_records: true,
        });
    }

    hasErrors = (fieldsError) => {
        return Object.keys(fieldsError).some(field => fieldsError[field]);
    }

    // 保存代理的更改
    handleSave = (row) => {
        delete row.key;

        let _partners = [...this.state.partners];
        let index = _partners.findIndex(item => row.pid === item.pid);
        let item = _partners[index];

        if ( this.partner_old.pid || this.partner_old.pid >= 0 ) {
            if ( this.partner_old.pid !== item.pid ) {
                this.partner_old = item;
            }
        } else {
            this.partner_old = item;
        }

        _partners.splice(index, 1, {
            ...item,
            ...row,
        });
        this.setState({ partners: _partners });
    }

    recordsRowRender = ( record ) => {
        const columns = [{
            title: '日期',
            dataIndex: 'daytime',
            key: 'daytime',
            align: 'center',
        }, {
            title: '金额(EOS)',
            dataIndex: 'quantity',
            key: 'quantity',
            align: 'center',
        }];
    
        let data = [];
        let pid = record.pid, records = this.state.records[pid];
        for ( let i = 0; records && i < records.length; i++ ) {
            data.push({
                key: i,
                pid: pid,
                day: records[i].day,
                daytime: new Date( records[i].day * 86400 * 1000 ).Format('yyyy/MM/dd'),
                quantity: records[i].quantity,
            });
        }
    
        return (
            <Table
                columns={columns}
                dataSource={data}
                rowSelection={this.recordsSelection}
                pagination={false}
                loading={this.state.loading_records}
            />
        );
    };

    render() {
        const {
            getFieldDecorator, getFieldsError, getFieldError, isFieldTouched,
        } = this.props.form;
        const partNameError = isFieldTouched('partName') && getFieldError('partName');
        const partRateError = isFieldTouched('partRate') && getFieldError('partRate');

        const _data_parts = [];
        const _partners = this.state.partners;
        for ( let i = 0; i < _partners.length; i++ ) {
            _data_parts.push({
                key: i,
                pid: _partners[i].pid,
                partner: _partners[i].partner,
                rate: _partners[i].rate,
            });
        }
        const _columns_parts = this.columns_parts.map((col) => {
            if (!col.editable) {
                return col;
            }

            return {
                ...col,
                onCell: record => ({
                    record,
                    editable: col.editable,
                    dataIndex: col.dataIndex,
                    title: col.title,
                    handleSave: this.handleSave,
                }),
            }
        });
        const _components = {
            body: {
              row: EditableFormRow,
              cell: EditableCell,
            },
        };

        return (
            <div>
                <Layout className='layout'>
                    <Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
                        <div className='header-box'>
                            <Switch className='main-kylin' checkedChildren="主网" unCheckedChildren="麒麟" defaultChecked onChange={this.switchNet} />
                            <a href='javascript:;' onClick={this.pay} className='pay'>
                                {<span><Icon type='pay-circle'/> 结算</span>}
                            </a>
                            <a href='javascript:;' onClick={this.accountLoginLogout} className='login-logout'>
                                {
                                    this.state.is_login === true ?
                                    <span><Icon type='user'/> {this.state.login_account}</span> :
                                    <span><Icon type='login'/> {this.state.login_account}</span>
                                }
                            </a>
                        </div>
                    </Header>
                    <Content>
                        <div className='add-partner'>
                            <Form layout="inline" onSubmit={this.addPartner}>
                                <FormItem
                                    validateStatus={partNameError ? 'error' : ''}
                                    help={partNameError || ''}
                                >
                                    {getFieldDecorator('partName', {
                                        rules: [{ required: true, message: 'Please input a partner name!' }],
                                    })(
                                        <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} type="text" placeholder="账号" />
                                    )}
                                </FormItem>
                                <FormItem
                                    validateStatus={partRateError ? 'error' : ''}
                                    help={partRateError || ''}
                                >
                                    {getFieldDecorator('partRate', {
                                        rules: [{ required: true, message: 'Please input a return rate!' }],
                                    })(
                                        <Input prefix={<Icon type="calculator" style={{ color: 'rgba(0,0,0,.25)' }} />} type="number" placeholder="返现率" />
                                    )}
                                </FormItem>
                                <FormItem>
                                        <Button
                                            type="primary"
                                            htmlType="submit"
                                            disabled={this.hasErrors(getFieldsError())}
                                        >添加代理</Button>
                                </FormItem>
                            </Form>
                        </div>
                        <Table
                            components={_components}
                            columns={_columns_parts}
                            dataSource={_data_parts}
                            expandedRowRender={this.recordsRowRender}
                            pagination={false}
                            loading={this.state.loading_partners}
                            style={{ width: '720px', margin: '30px auto' }}
                        />
                    </Content>
                </Layout>
            </div>
        );
    }

    componentDidMount() {
        this.props.form.validateFields();

        this.eosjs = Eos({
            httpEndpoint: `${this.network.protocol}://${this.network.host}:${this.network.port}`,
            chainId: this.network.chainId,
        });

        document.addEventListener('scatterLoaded', scatterExtension => {
            this.scatter = window.scatter;
            window.scatter = null;

            if (this.scatter && this.scatter.identity) {
                const account = this.scatter.identity.accounts.find(account => account.blockchain === 'eos');
                if ( account.name !== info_contract_account ) {
                    Message.warning('该账号不是管理员账号');
                }
    
                this.trx_option = {authorization: [`${account.name}@${account.authority}`]};
                this.setState({ login_account: account.name });
                this.setState({ is_login: true });
            }
        });

        this.getPartners();
    }
}

export default Form.create()(Trustbetinfo);
