import React, { Component } from 'react';
import { Table } from 'antd';
import Eos from 'eosjs';
import {
    kylin_net, main_net, sicbo_contract_account,
} from '../Common';

class Trustbetgame extends Component {
    constructor(props) {
        super(props);

        this.state = {
            bets_notreveal: [],
            is_fetching_bets: true,
        }

        this.network = main_net;
        this.columns = [{
            title: '序号',
            dataIndex: 'num',
            key: 'num',
            width: '5%',
        }, {
            title: '表ID',
            dataIndex: 'tbid',
            key: 'tbid',
            width: '5%',
        }, {
            title: '时间',
            dataIndex: 'txtime',
            key: 'txtime',
            width: '15%',
        }, {
            title: '玩家',
            dataIndex: 'player',
            key: 'player',
            width: '15%',
        }, {
            title: '下注',
            dataIndex: 'quantity',
            key: 'quantity',
            width: '10%',
        }, {
            title: 'memo',
            dataIndex: 'memo',
            key: 'memo',
            width: '50%',
        }];
    }

    fetchBets = (lower=0) => {
        this.eosjs.getTableRows({
            json: true,
            code: sicbo_contract_account,
            scope: sicbo_contract_account,
            table: 'bets',
            lower_bound: lower,
            limit: 50,
        })
        .then(data => {
            let _threshold = 10 * 60;  // 10 minutes
            let _now_seconds = Math.floor(Date.now() / 1000);
            let _bets_notreveal = this.state.bets_notreveal;

            for (let bet of data.rows) {
                // id: 0
                // opt: "u:b6300cf0-dad5-4c40-a934-4afd2d1f9cf2;2:1000"
                // player: "oobpcotsubep"
                // quantity: "0.1000 EOS"
                // txtime: 1542468102
                // uid: "b6300cf0-dad5-4c40-a934-4afd2d1f9cf2"
                if (_now_seconds - bet.txtime > _threshold) {
                    // 超过10分钟，视为卡币
                    _bets_notreveal.push({
                        key: _bets_notreveal.length,
                        num: _bets_notreveal.length+1,
                        tbid: bet.id,
                        txtime: new Date(bet.txtime*1000).Format('yyyy-MM-dd hh:mm:ss'),
                        player: bet.player,
                        quantity: bet.quantity,
                        memo: bet.opt,
                    });
                }
            }

            if (_bets_notreveal.length > this.state.bets_notreveal.length) {
                this.setState({ bets_notreveal: _bets_notreveal });
            }
            
            if (data.more) {
                this.fetchBets(data.rows[data.rows.length-1].id + 1);
            } else {
                this.setState({is_fetching_bets: false});
            }
        });
    }

    componentDidMount() {
        this.eosjs = Eos({
            httpEndpoint: `${this.network.protocol}://${this.network.host}:${this.network.port}`,
        });

        this.fetchBets();
    }

    render() {
        return (
            <div style={{ paddingTop: '80px' }}>
                <h2>骰宝卡币统计</h2>
                <span>Note: 超过10分钟未开奖的视为卡币！！</span>
                <Table
                    loading={this.state.is_fetching_bets}
                    columns={this.columns}
                    dataSource={this.state.bets_notreveal}
                    style={{ width: '80%', margin: 'auto' }}
                />
            </div>
        );
    }
}

export default Trustbetgame;
